@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
body {
  font-family: 'Inter', sans-serif;
  background: #fff;
}

* {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
}
.clearfix{
  clear:both;
}
a{
  text-decoration:none;
  transition:all 500ms ease;
  -moz-transition:all 500ms ease;
  -webkit-transition:all 500ms ease;
  -ms-transition:all 500ms ease;
  -o-transition:all 500ms ease;
}
a:hover{
  text-decoration:none;
  outline:none;
}

ul {
  list-style: none;
}
.App {
	margin-right:auto;
	margin-left:auto;
  padding-right: 10px;
  padding-left: 10px;
}

@media screen and (max-width: 560px) {
  .App {
    padding-left: 1px;
    padding-right: 1px;
  }
}
.home {
    display: flex;
}
/*left section of the home page */

.home .home_left {
    flex: 1 1;
    padding: 2px;
}
.home_left .quick_about {
    margin-top: 10px;
    margin-bottom: 8px;
    background-image: url(/wynescentech-solutions/static/media/bg_sm.8c9e76ed.PNG);
  /* these will reset other properties, like background-position, but it does know what you mean 
  background-color: red;
  background-image:
    radial-gradient(
      circle at top right,
      yellow,
      #f06d06 50%
    );
*/
}
.quick_about h3 {
    color: #fff;
    width: 90%;
    font-size: 1rem;
    padding: 5px 5px 15px 3px;
    background-color: rgb(1, 45, 83);
  background-image:
    radial-gradient(
      circle at top right,
      rgb(173, 172, 169),
      #024a65 50%
    );
    border-top: 3px solid #df0707;
    border-bottom-right-radius: 55px 95%;
}
.quick_about .service {
    background-image: url(/wynescentech-solutions/static/media/bg_sm.8c9e76ed.PNG);
    padding-inline-start: 5px;
}

.quick_about .service h4 {
    font-size: 0.9rem;
    color: #fff;
    font-weight: 900;
    padding-top: 10px;
    padding-bottom: 5px;
}
.quick_about .service h4 span {
    color: #faed00;
}
.quick_about .service .service_descr {
    margin-left: 5px;
    margin-top: 10px;
    color: #fff;
    font-size: 0.9rem;
    padding-left: 3px;
    border-left: 4px solid #000;
    font-weight: 600;
}
.quick_about .service .far_left {
    background-color: #fbfbfb;
    margin: 15px 0 10px -5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 4px 10px 4px 5px;
    border-top-right-radius: 10px;
    border-bottom-right-radius:10px ;
    box-shadow: 0 4px 4px #0000006a;
    font-size: 1.1rem;
    display: flex;
}
.service .far_left .shopping_cart {
    position: relative;
}
.shopping_cart div {
    background: #fa8b02;
    border-radius: 50%;
    padding: 8px;
    border: 1px solid #fff;
    position: absolute;
    top: -12px;
    box-shadow: 0 0 2px 2px #faed00;
    z-index: 1;
}
.shopping_cart i {
    color: #fff;
    font-size: 1.4rem;
}
.far_left h5 {
    font-size: 1.1rem;
    padding-left: 45px;
}
ul.service_list {
    padding: 5px 2px 5px 10px;
    font-size: 0.9rem;
    display: flex;
    flex-direction: column;
}
ul.service_list li {
    color: #e6e4e4;
    font-weight: 800;
    margin-bottom: 5px;
    display: inline-block;
}
ul.service_list li:hover {
    transform: scale(1.05);
}
ul.service_list i {
    padding-right: 2px;
    color: #000;
    font-size: 0.8rem;
}
/*home right section */
.home .home_right {
    flex: 2 1;
    padding: 2px;
}

.web_packages {
    padding-left: 10px;
    background-image: url(/wynescentech-solutions/static/media/bg.ce404b15.PNG);
    margin-top: 10px;
}
.home_right .web_packages h1 {
    font-size: 1rem;
    border-bottom: 1px solid #d2d0d0;
    color: blue;
}

.basic {
    border-bottom: 1px solid #c0bfbf;
}
.basic .contents {
    display: flex;
}
.basic .contents h3 {
    font-size: 1rem;
    color: #07026d;
    font-weight: 700;
    padding-bottom: 3px;
}
.basic .contents h4 {
    font-size: 0.9rem;
}
.contents .features {
    padding: 0 5px 0 10px;
    flex: 2 1;
}
.features ul li {
    margin-bottom: 4px;
    font-size: 0.9rem;
}
.features ul li i {
    color: #a70606;
    font-size: 0.8rem;
    padding-right: 4px;
}
.features .disclaimer {
    padding-top: 5px;
    font-size: 0.7rem;
}
.disclaimer span {
    font-weight: 900;
    color: #df0707;
}
/* Ongoing cost section */ 
.cost_advert {
    flex: 3 1;
    display: flex;
}
.variable_cost {
    padding-left: 15px;
    flex: 2 1;
}
.variable_cost ul.costs {
    padding: 2px 0 0 5px;
}
.variable_cost ul.costs li {
    font-size: 0.8rem;
    margin-bottom: 5px;
}
ul.costs li i {
    font-size: 0.7rem;
    color: #a70606;
    padding-right: 3px;
}
ul.costs li p {
    font-size: 0.8rem;
    color: rgb(105, 105, 104);
}

/* prcining banner */
.basic .pricing {
    display: block;
    box-sizing: border-box;
    border-radius: 2px;
    background: #3cadce;
    background-image: url(/wynescentech-solutions/static/media/engine.b800dff5.jpg);
    font-size: 20px;
    text-align: center;
    text-decoration: none;
    color: white;
    max-width: 180px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-bottom: 2px;
    flex: 1 1;
}
.pricing .price_target {
    padding: 14px;
}
.pricing h4 {
    margin: 0;
    font-size: 20px;
    font-style: italic;
    text-transform: uppercase;
}
.pricing .price_descr {
    margin: 10px 0;
    font-size: 15px;
    font-weight: 300;
    line-height: 1.45;
}
.pricing .price_descr span {
    font-weight: 900;
    color: #e9e8e6;
}
.pricing .order {
    display: block;
    padding: 10px 0;
    border-radius: 0 0 2px 2px;
    font-size: 13px;
    background: #255571;
    font-weight: 700;
    text-transform: uppercase;
    color: white;
    transition: background 0.5s ease;
}
.pricing .order:hover {
    background: #f29e02;
}

/*Banner section */
.banner {
    width: 100%;
}
/* Advanced websites section */
.advanced_websites {
    background-color: #fff;
  background-image:
    radial-gradient(
      circle at top right,
      rgb(253, 253, 248),
      #fff 50%
    );
    margin-top: 10px;
    border-top: 3px solid #f1f1f38b;
}

/*Ongoing cost table */
.ongoing_cost {
    background: #fff;
    margin-top: 10px;
    border-top: 2px solid #a70606;
}
@media screen and (max-width: 768px) {
    .home .home_left {
        flex: 2 1;
    } 
    .home .home_right {
        flex: 3 1;
    }
    .basic .contents {
        flex-direction: column;
    }
    .contents .features {
        padding: 0 5px 0 5px;
        flex: 1 1;
    }
    .variable_cost {
        padding: 14px 5px 12px 5px;
        flex: 1 1;
    }
    .basic .pricing {
        margin-top: 5px;
    }
    .pricing .price_target {
        padding: 10px;
    }
    .pricing .order {
        padding: 8px 0;
    }
}
@media screen and (max-width: 560px) {
    .home {
        flex-direction: column;
    }
    .home .home_left {
        flex: 1 1;
    } 
    .home .home_right {
        flex: 1 1;
    }
    .web_packages {
        padding-left: 0;
    }
    ul.service_list {
        display: flex;
        flex-direction: column;
    }
    ul.service_list li:hover {
        transform:scaleY(1.04);
    }
    .banner {
        width: auto;
        padding: 6px;
    }
}

@media screen and (max-width: 480px) {
    .quick_about .service .far_left {
        margin: 15px 0 10px -4.5px;
    }
    .home .home_left {
        width: 97.5%;
    } 
    .home .home_right {
        width: 97.5%;
    }
}
.advert {
    width: auto;
    background: #018096;
    height: 5rem;
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    box-shadow: 0 5px 15px rgb(0, 0, 0, 0.35);
    cursor: pointer;
    border-radius: 0.3rem;
    margin:5px 0 5px 0;
}
.advert:hover {
    transform: scale(1.03);
    box-shadow: 0 5px 15px 5px rgb(0, 0, 0, 0.55);
}

.banner_image {
    height: auto;
    flex: 1 1;
}
.banner_image > img {
    height: 5rem;
    width: 10rem;
    object-fit: cover;
    border-radius: 0 2em 2em 0/0 3em 3em 0;
    box-shadow: 6px 10px 1px 6px #00455e;
}
.advert .banner_text {
    height: 5em;
    flex: 4 1;
    display: flex;
    justify-content: center;
    text-align: center;
    transition: 1s all linear;
    padding: 5px 0 5px 0;
}
.banner_text .tx-left {
    flex: 3 1;
}
.banner_text h4 {
    font-size: 15px;
    text-align: center;
    color: #fff;
    font-weight: 900;
}
.banner_text p {
    color: antiquewhite;
    font-size: 13px;
}
.banner_text .tx-right {
    padding-top:15px;
    margin-left: 1px;
    margin-right: 2px;
    flex: 1 1;
}
.tx-right button {
    border-radius: 2px;
    width: auto;
    background: #fdfbfb;
    padding: 5px 5px;
}
.tx-right button a {
    color: #026269;    
    font-size: 1rem;
    font-weight: 900;
}
.tx-right button:hover {
    background: #d3d3d3;
}


@media screen and (max-width: 768px) {
    .banner_image {
        width: 6rem;
    }
    .banner_image > img {
        width: 6rem;
    }
    .banner_text h4 {
        font-size: 13px;
    }   
    .tx-right button a {
        font-size: 0.8rem;
    } 
}

@media screen and (max-width: 560px) {
    .banner_image > img {
        width: 4rem;
    }
}
@media screen and (max-width: 480px) {
    
    .banner_text .tx-right {
        padding-top:8px;
    }
    .tx-right button {
        border-radius: 50%;
        height: 55px;
        width: 55px;
        background: #fdfbfb;

    }
}
.premium_packages h1 {
    font-size: 1.2rem;
    border-bottom: 1px dotted #f42a02;
    padding: 5px 0 5px 5px;
}
.premium_packages p {
    padding: 5px;
    font-size: 1rem;
}
.the_packages {
    padding: 5px;
    display: flex;
    flex-wrap: wrap;
    margin-right: 5px;
}
.package_name {
    flex: 0 0 33.333333%;
    max-width: 33.333333%;
    box-shadow: 0 0 3px 3px#bfbfbf61;
    border: none;
    border-radius: 2px;
}
.package_name .title h3 {
    font-size: 1rem;
    padding: 5px 1px 2px 5px;
}
.package_name .title .underline {
    width: auto;
    display: flex;
    margin-left: 5px;
    width: 60%;
}
.title .underline .tl {
    border: 3px solid #f42a02;
    width: 20%;
}
.title .underline .tr {
    border: 3px solid #110156;
    width: 80%;
}
.the_packages .items {
    padding: 5px 0 5px 5px;
}
.the_packages .items h4 {
    font-size: 1rem;
    color: #f42a02;
}
.the_packages .items .pkg_list {
    padding-top: 5px;
}
.pkg_list li {
    margin-bottom: 4px;
    font-size: 0.9rem;
}
.pkg_list li i {
    font-size: 0.8rem;
    padding: 0 3px 0 3px;
    color: #f42a02;
}

.business {
    background-color: #ededed;
  background-image:
    radial-gradient(
      circle at bottom right,
      rgba(160, 251, 139, 0.664),
      #fff 60%
    );
}
.ecommerce {
    background-color: #ededed;
  background-image:
    radial-gradient(
      circle at bottom right,
      rgba(249, 151, 46, 0.664),
      #fff 60%
    );
}
.ecommerce .title h3 {
    color: #d57603;
}
.corporate {
    background-color: #ededed;
  background-image:
    radial-gradient(
      circle at bottom right,
      rgba(123, 171, 254, 0.549),
      #fff 60%
    );
}
.corporate .title h3 {
    color: #025fb5;
}

/*subscribe go section */
.subcribe_go {
    text-align: center;
    justify-content: center;
    background: #012e58;
    padding: 5px 3px 5px 3px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    float: right;
    margin-right: 4px;
    margin-bottom: 3px;
    border-radius: 2px;
}
.subcribe_go a {
    color: #ededed;
    font-size: 1.1rem;
    font-weight: 900;
}
.subcribe_go a:hover {
    color: #d57603;
}
.subcribe_go a span {
    margin-right: 3px;
}
@media screen and (max-width: 480px) {
    .package_name {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 10px;
    }
}
.recurrent_cost {
    padding: 5px;
}
.recurrent_cost h1 {
    font-size: 1.2rem;
    padding-bottom: 5px;
}
.recurrent_cost p {
    font-size: 0.9rem;
}
.cost_table {
    width: 100%;
    margin-top: 10px;
    padding: 10px;
    background: #f0efef7a;
    margin-bottom: 10px;
}
.cost_table tr th {
    text-align:start;
    border-bottom: 1px solid #b7b4b4;
    font-size: 0.9rem;
}
.cost_table tr td {
    border-bottom: #fff;
    padding-bottom: 5px;
    color: rgb(1, 58, 58);
    font-size: 0.8rem;
}
.about_us {
    display: flex;
    flex-direction: column;
}
.about_us .web_design {
    display: flex;
}
.web_design .about_details:nth-child(1) {
    flex: 1 1;
}
.web_design .about_details:nth-child(2) {
    flex: 1 1;
}
.web_design .about_details:nth-child(3) {
    flex: 2 1;
}
.footer {
    background: #012960;
    color: #fff;
}
.do_search {
    position: absolute;
    right: 15px;
    top: 162px;
}
.do_search .input-search {
    height: 17px;
    width: 165px;
    border-style: none;
    padding: 10px;
    font-size: 15px;
    letter-spacing: 2px;
    outline: none;
    border-radius: 20px;
    transition: all .5s ease-in-out;
    background-color: #013b5d;
    padding-right: 40px;
    color:#fff;
}
.do_search .btn_search {
    position: absolute;
    top: 0;
    right: 7px;
    background:transparent;
}

@media screen and (max-width: 768px) {
    .do_search {
        top: 170px;
    } 
}

@media screen and (max-width: 560px) {
    .do_search {
        top: 148px;
    }
}
.nav_menu {
    width: 100%;
    background: #f7f5f5;
    z-index: 2;
}
.nav_menu .top_nav {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #fff;
}
.nav_menu .top_nav ul.social_menu,
.nav_menu .top_nav ul.account_menu {
    display: inline-flex;
    padding: 5px;
}
.nav_menu .top_nav ul.social_menu li,
.nav_menu .top_nav ul.account_menu li{
    padding-right: 10px;
}
.nav_menu .top_nav ul.social_menu li i {
    font-size: 1.3rem;
}
.nav_menu .top_nav ul.social_menu li i:hover {
    color: #f48f03;
}
ul.account_menu button {
    padding: 5px 15px 5px 15px;
    font-size: 0.9rem;
    border-radius: 25px;
}
ul.account_menu button.login_btn {
    background: transparent;
    border: 1px solid #01427b;
}
ul.account_menu button.login_btn:hover {
    transform: scale(1.03);
    box-shadow: #0241613f 1px 1px 3px 3px;
}
ul.account_menu button.login_btn a{
    color: #01427b;
    text-transform: uppercase;
    font-weight: 800;
}
ul.account_menu button.signup_btn {
    background: rgb(2,0,36);
background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(8,148,161,1) 0%, rgba(0,212,255,1) 100%);
}
ul.account_menu button.signup_btn:hover {
    transform: scale(1.05);
    box-shadow: #0241613f 1px 1px 3px 3px;
}
ul.account_menu button.signup_btn a{
    color: #fff;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 2px;
}

/* Middle section styling */
.middle_nav {
    display: flex;
    justify-content: space-between;
    padding-top: 10px;
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(4,158,189,1) 0%, rgba(236,244,245,0.8330523887377107) 100%);
    border-bottom: 1px solid #fff;
}
.middle_nav .logo {
    flex: 2 1;
    height: 77px;
}
.middle_nav .banner_img {
    flex: 1 1;
    height: 77px;
}

.logo img {
    width: 300px;
    height: 77px;
}

/* Bannner contact details */
.banner_img {
    display: flex;
    flex-direction: column;
}
.banner_img span{
    padding: 5px 0 5px 0;
    color: #ab3202;
    font-weight: 700;
    font-size: 1rem;
}
.banner_img span .bld {
    color: #e54303;
}

/* bottom navigation styling */

.bottom_nav {
    display: flex;
    justify-content: space-between;
    background: #01396ace;
    width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.bottom_nav .menu_items {
    width: 95%;
    padding: 5px 0 5px 5px;
}

.menu_items ul.menu_list {
    display: inline-flex;
}
ul.menu_list li {
    padding: 0 8px 0 0;
}

ul.menu_list li a {
    color: #fff;
    font-weight: 500;
}
ul.menu_list li a:hover {
    color: #fa9c18;
    font-weight: 800;
}
.bottom_nav .search_bar {
    width: 5%;
    display: inline-flex;
}
.search_bar i {
    padding: 5px 2px 5px 0;
    color: #fff;
    font-size: 1.5rem;
}
.search_bar i:hover {
    color: #f48f03;
    transform: scale(1.1);
}

.menu_icon {
    display: none;
}
.menu_items ul.menu_list li a.active {
    color: #fa9c18;
}

@media screen and (max-width: 768px) {
    .logo img {
        width: 220px;
    }
    .banner_img span{
        padding: 2px 0 2px 0;
        font-weight: 700;
        font-size: 0.9rem;
    }
    .bottom_nav .menu_items {
        width: auto;
        max-width: 30%;
    }
    .menu_items ul.menu_list {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 170px;
        background-color: #f9fafa;
        padding: 3px;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        z-index: 2;
    }
    .menu_icon {
        color: #fff;
        font-size: 1.5rem;
        display: block;
    }
    .menu_icon div:hover {
        cursor: pointer;;
    }
    .bottom_nav .search_bar {
        width: 10%;
    }
    .search_bar i {
        padding: 8px 0 5px 25px;
    }
    ul.menu_list li a {
        color: #151515;
        font-weight: 600;
    }
    ul.menu_list li {
        padding: 0 5px 0 0;
        margin-bottom: 15px;
    }
}

@media screen and (max-width: 560px) {
    .search_bar i {
        padding: 8px 0 5px 2px;
    }
    .logo img {
        width: 180px;
        height: 55px;
    }
    .banner_img span {
        font-size: 0.75rem;
        padding: 0 3px 0 0;
    }
    .menu_items ul.menu_list {
        top: 150px;
    }
}
@media screen and (max-width: 480px) {
    ul.account_menu button {
        padding: 5px 7px 5px 7px;
        font-size: 0.6rem;
        border-radius: 25px;
    }
    .middle_nav {
        flex-direction: column;
    }
    .banner_img {
        text-align: center;
        justify-content: center;
    }
    .banner_img span {
        padding: 3px 0 3px 0;
        font-size: 0.8rem;
    }
    .menu_items ul.menu_list {
        top: 190px;
    }

}

